import React, { Component } from 'react'
import PureRenderMixin from 'react-addons-pure-render-mixin'
import CommunityList from './CommunityList'
import EmInput from '@/components/EmInput'
import Tab from '@/components/Tab'
export default class CommunityFortune extends Component {
  constructor () {
    super()
    this.shouldComponentUpdate = PureRenderMixin.shouldComponentUpdate.bind(this);
    this.state = {
      active:0,
      tabIndex:0
    }
    this.handelSearch = this.handelSearch.bind(this)
    this.getTabIndex = this.getTabIndex.bind(this)
  }
  handelSearch = (text) => {

  }
  handelChange= (index) => {
    if(index === this.state.tabIndex){
      return ;
    }
    this.setState({
      tabIndex:index
    })
  }
  getTabIndex = (index) => {
    if(index === this.state.active){
      return ;
    }
    this.setState({
      active:index
    })
  }
  render() {
    let { active } = this.state
    let list = [1]
    let tabArr = ['发现','动态']
    return (
      <div className="wrapper">
        <div className="search-input">
          <span className="location-box">全国</span>
          <div className="searchinput-box">
            <EmInput onChange={this.handelSearch} />
          </div>
        </div>
        <Tab callback={ this.getTabIndex } active={active} tabs={['动态','关注列表']} />
        <CommunityList list={ list } tab={active} />
        <ul className="fortune-fixed">
          {
            tabArr.map((item,index)=>{
              return (
                <li onClick={this.handelChange.bind(this,index)} className={ index=== this.state.tabIndex && 'active'} key={index}>
                  <span class="has-dot">{item}</span>
                </li>
              )
            })
          }
        </ul>
      </div>
    )
  }
}
